<template>
  <div class="slider">
    <div class="slider-img" @mouseover="clearInv" @mouseout="runInv">
      <transition-group name="slider">
        <div v-for="(item, index) in slides" v-if="nowIndex === index" class="imgdiv" :key="index">
          <div class="container" style="height:700px;position:relative">
            <img
              :src="item.src"
            />
          </div>
        </div>
      </transition-group>
      <div class="slider-icon">
        <ul>
          <li v-for="(item, index) in slides" @click="goto(index)" @mouseover="goto(index)">
            <a :class="{ active: index === nowIndex }"></a>
          </li>
        </ul>
      </div>
    </div>
    <div class="head">
      <div class="container">
        <div class="head-item">
          <div>
            <img src="../../../assets/home/icon-weiyan.png"
            class="icon-weiyan">
          </div>
          <div>
            <div>
              <div class="text first">Wei Yan Thailand</div>
              <div class="text">{{$t('m.aboutUs1')}}</div>
            </div>
          </div>
        </div>
        <div class="head-item">
          <router-link :to="{path:'/ThaiPayMall'}">
            <div>
              <img src="../../../assets/home/icon-thai-pay-mall.png" class="icon-weiyan">
            </div>
            <div>
              <div>
                <div class="text first">ThaiPayMall</div>
                <div class="text">{{$t('m.aboutUs2')}}</div>
              </div>
            </div>
          </router-link>
        </div>
        <div class="head-item">
          <router-link :to="{path:'/express'}">
            <div>
              <img src="../../../assets/home/icon-locker.png"
              class="icon-weiyan">
            </div>
            <div>
              <div>
                <div class="text first">Smart Locker</div>
                <div class="text">{{$t('m.aboutUs3')}}</div>
              </div>
            </div>
          </router-link>
        </div>
        <div class="head-item">
          <div>
            <img src="../../../assets/home/icon-lime.png"
            class="icon-weiyan">
          </div>
          <div>
            <div>
              <div class="text first">Lime Fintech</div>
              <div class="text">{{$t('m.aboutUs4')}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    slides: {
      type: Array,
      default: []
    },
    inv: {
      type: Number,
      default: 1500
    }
  },
  data () {
    return {
      nowIndex: 0
    }
  },
  watch: {
  },
  computed: {
    prevIndex () {
      if (this.nowIndex === 0) {
        return this.slides.length - 1
      } else {
        return this.nowIndex - 1
      }
    },
    nextIndex () {
      if (this.nowIndex === this.slides.length - 1) {
        return 0
      } else {
        return this.nowIndex + 1
      }
    }
  },
  methods: {
    goto (index) {
      this.nowIndex = index
    },
    runInv () {
      this.invId = setInterval(() => {
        this.goto(this.nextIndex)
      }, this.inv)
    },
    clearInv () {
      clearInterval(this.invId)
    }
  },
  mounted () {
    this.runInv()
  }
}
</script>

<style scoped lang='stylus'>
&.slider-enter
  transform translateX(100%)
&.slider-enter-active, .slider-leave-active
  transition all 1.2s
&.slider-leave-to
  transform translateX(-100%)
.slider
  width 100%
  height 700px
  position relative
  overflow hidden
  .slider-img
    position absolute
    top 0
    left 0
    width 100%
    height 100%
    .imgdiv
      display inline-block
      overflow hidden
      width 100%
      height 100%
      position absolute
      top 0
    img
      position absolute
      width 1920px
      left -360px
    .slider-icon
      width 100%
      position absolute
      bottom 140px
      left 0
      ul
        list-style none
        float left
        position relative
        left 50%
        li
          margin 0 10px
          float left
          position relative
          right 50%
          a
            display block
            width 8px
            height 8px
            border 1px solid #fff
            border-radius 50%
            background #fff
            vertical-align middle
            cursor pointer
          .active
            width 14px
            height 14px
            position relative
            top -3px
  .head
    position absolute
    bottom 0
    width 100%
    background-color #473856
    opacity 0.8
    text-align center
    .head-item
      width 276px
      padding 40px 10px
      display inline-table
      cursor pointer
      div
        display table-cell
        vertical-align middle
        .text
          padding-left 20px
          display block
          text-align left
          font-size 14px
          color #7d5f9a
        .first
          margin-bottom 10px
          color #f0e3fb
          font-size 18px
</style>



