import VSlider from './item/slider'
import More from './item/more'
import Detail from './item/detail'
export default {
  data () {
    return {
      slideSpeed: 3000,
      slides: [
        {
          src: require('../../assets/home/pic-1.png')
        },
        {
          src: require('../../assets/home/pic-1.png')
        },
        {
          src: require('../../assets/home/pic-1.png')
        },
        {
          src: require('../../assets/home/pic-1.png')
        },
        {
          src: require('../../assets/home/pic-1.png')
        }
      ]
    }
  },
  created () {
    window.scrollTo(0, 0)
  },
  components: {
    VSlider,
    More,
    Detail
  }
}
</script>

